.nav {
    position: fixed;
    top: 0;
    padding: 20px;
    width: 100%;
    height: 1.7rem;
    z-index: 1;

    /* Animations */
    transition-timing-function: ease-in;
    transition: all 0.45s;
}

.nav_contents {
    display: flex;
    justify-content: space-between;
}
.nav_black {
    background-color: #1b1c1e;
}

.nav_logo {
    position: fixed;
    left: 1rem;
    top: 6px;
    width: 5.7rem;
    object-fit: contain;
    margin: 0.8rem 2px 2px 2%;
    top: 1%;
    cursor: pointer;
}

.nav_avatar {
    position: fixed;
    right: 0;
    width: 2.7rem;
    border-radius: 5%;
    margin: 0 3.2% 1px 2px;
    top: 1.6%;
    cursor: pointer;
}

@media screen and (min-width: 1000px) {
    .nav_logo {
        width: 6.5rem;
        /* margin-left: 17rem; */
    }
    .nav_avatar {
        width: 2.7rem;
        /* margin-right: 17rem; */
    }
    .nav {
        height: 2rem;
    }
    
}
@media screen and (min-width: 1280px) {
    .nav_logo {
        width: 7.7rem;
        margin-left: 11%;
        top: 0.4%;
    }
    .nav_avatar {
        width: 3rem;
        margin-right: 11%;
    }

    .nav {
        height: 2rem;
    }
    
}