.row {
    color: floralwhite;
    margin-left: 20px;
}

.row__posters {
    display: flex;
    overflow-y: hidden;
    overflow-x: scroll;
    padding: 20px;
}

.row__posters::-webkit-scrollbar {
    display: none;
}

.row__poster {
    /* height: 14rem; */
    max-height: 16rem;
    object-fit: contain;
    margin-right: 0.68rem;
    border-radius: 1px;
    width: 100%;
    transition: transform 430ms;
}

.row__poster:hover {
    transform: scale(1.1);
    opacity: 1;
    filter: drop-shadow(6px 6px 11px #0d0d0d)
}

.row__posterLarge {
    max-height: 15rem;
}
.row__posterLarge {
    max-height: 18rem;
}

